import commons from "@/util/commons";
import query from "@/services/query";
import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  Table,
  TableColumn,
  Select,
  Option,
} from "element-ui";
export default {
  components: {
    InlineActionsTable: () =>
      import("./Finances/RegularTables/InlineActionsTable"),
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Select.name]: Select,
    [Option.name]: Option,
    SearchBarWithAvatar: () =>
      import("./components/layout/SearchBarWithAvatar.vue"),
  },
  data() {
    return {
      showStatementHistory: false,
      showStatement: false,
      showPreview: false,
      previewData: {},
      studioName: "",
      address: "657 W Lake St, Chicago",
      phone: "+1 400 820 8820",
      salesTax: {
        simple: "6.00%",
        options: [
          {
            label: "4.00%",
            value: "0.04",
          },
          {
            label: "6.00%",
            value: "0.06",
          },
          {
            label: "8.00%",
            value: "0.08",
          },
          {
            label: "10.00%",
            value: "0.1",
          },
        ],
      },

      logo: "",
      studioInfo: null,
      teacherInfo: null,
      billTo: {
        name: "",
        street: "",
        city: "",
        phone: "",
      },
    };
  },
  async created() {
    let self = this;

    let user = JSON.parse(localStorage.getItem("auth_user"));
    let teacher = await query.getTeacherInfoById(user.uid);
    console.log(teacher);
    self.teacherInfo = teacher;
    self.logo = commons.studioLogoPath(teacher.studioId);
    let studio = await query.getStudioInfoById(teacher.studioId);
    console.log(studio);
    self.studioInfo = studio;
  },
  mounted() {
    $("html body").css("overflow", "hidden")
  },
  watch: {
    previewData: {
      handler(newVal, oldVal) {
        if (newVal.type === "invoices") {
          let subTotal = parseFloat(newVal.data.subTotal);
          let tax = parseFloat(newVal.data.salesTax);
          let fee = parseFloat(newVal.data.lateFee);
          let balance = parseFloat(newVal.data.balance);
          this.previewData.data.total = parseFloat(
            subTotal + subTotal * tax + subTotal * fee + balance
          )
            .toFixed(2)
            .toString();
        }
      },
      deep: true,
    },
  },
  methods: {
    preview(data) {
      let self = this;
      self.previewData = data;
      console.log("preview: ", self.previewData);
      self.showPreview = true;
      if (data.type == "invoices") {
        self.billTo.name = self.previewData.data.name;
        self.billTo.street = self.previewData.data.street;
        self.billTo.city = self.previewData.data.city;
        self.billTo.phone = self.previewData.data.phone;
      } else if (data.type == "payouts") {
        self.showStatement = true;
      }
    },
    hidePreview() {
      this.showPreview = false;
      this.showStatement = false;
      this.showStatementHistory = false;
      this.previewData = {};
    },
    numberToPercent(number) {
      return this.$tools.numberToPercent(number);
    },
    calculateSalesTax() {
      let self = this;
      let subTotal = self.previewData.data.subTotal;
      let salesTax = self.previewData.data.salesTax;
      return (parseFloat(subTotal) * parseFloat(salesTax)).toFixed(2);
    },
    calculateLateFee() {
      let self = this;
      let subTotal = self.previewData.data.subTotal;
      let lateFee = self.previewData.data.lateFee;
      return (parseFloat(subTotal) * parseFloat(lateFee)).toFixed(2);
    },
    focus(e) {
      e.target.classList.add("tk-editable-focus");
    },
    blur(e) {
      e.target.classList.remove("tk-editable-focus");
    },
    clickPercent(e) {
      e.target.style.display = "none";
      e.target.nextElementSibling.innerText = e.target.innerText;
      e.target.nextElementSibling.style.display = "inline-block";
      e.target.nextElementSibling.focus();
    },
    inputPercent(e) {
      console.log(parseFloat(e.target.innerText));
      if (isNaN(parseFloat(e.target.innerText))) {
        e.target.style.borderColor = "#fa897b";
      } else {
        e.target.style.borderColor = "#71d9c2";
      }
    },
    blurPercent(e, type) {
      let value = e.target.innerText;
      let num = parseFloat(value);
      if (!isNaN(num)) {
        if (type === 1) {
          this.previewData.data.salesTax = parseFloat(num / 100);
        } else {
          this.previewData.data.lateFee = parseFloat(num / 100);
        }
      }
      e.target.previousElementSibling.style.display = "inline-block";
      e.target.innerText = "";
      e.target.style.borderColor = "#71d9c2";
      e.target.style.display = "none";
    },
  },
};
</script>
<style scoped>
.tk-percent-border {
  border-bottom: 2px solid white;
  cursor: text;
}
.tk-editable-mouse-over,
.tk-editable-focus {
  padding: 0 8px;
  border: 2px solid #71d9c2;
  border-radius: 2px;
}
.tk-editable-mouse-over2,
.tk-editable-focus2 {
  padding: 0 8px;
  border-bottom: 2px solid #71d9c2;
}
</style>
